import {configureStore,combineReducers,getDefaultMiddleware }from '@reduxjs/toolkit'
import { 
  persistReducer,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER } from "redux-persist";

//import storageSession from "redux-persist/lib/storage/session";  // sessionStorage
import storage from 'redux-persist/lib/storage'; // localstorage

import searchDataSlice from './searchDateSlice';
import userIdSlice from './userIdSlice';
import serverUrlSlice from './urlSlice';
import menuSlice from './menuSlice';


//slice reducer  
const rootReducer = combineReducers({
  search:searchDataSlice.reducer,
  userId:userIdSlice.reducer,
  serverUrl:serverUrlSlice.reducer,
  menu:menuSlice.reducer
});

const persistConfig: any = {
  key: "root",
  storage, //   .
  whitelist: ["search","menu","userId"], //         state     
  //blacklist: ["userId"] //    
};

const persistedReducer = persistReducer(persistConfig, rootReducer);

//slice  store
export const store = configureStore({ 
  reducer: persistedReducer,
  middleware: getDefaultMiddleware => getDefaultMiddleware({ 
    serializableCheck: {
       ignoreActions: true //FLUSH,REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER  
    }
  }),
});

  export default store;
  //typescript    state      . 
  export type RootState = ReturnType<typeof store.getState>
  export type AppDispatch = typeof store.dispatch

